import React from "react";
import styles from "./Footer.module.css";
import { FaFacebookF, FaInstagram, FaLinkedin } from "react-icons/fa";
import { NavLink } from "react-router-dom";

const Footer = () => {
  return (
    <footer>
      <div>
        <NavLink to="https://www.facebook.com/profile.php?id=100029246772125" target="_blank" className={styles.socialIcons}>
          <FaFacebookF />
        </NavLink>
        <NavLink to="https://www.instagram.com/dheerajsharma6468/" target="_blank" className={styles.socialIcons}>
          <FaInstagram />
        </NavLink>
        <NavLink to="https://www.linkedin.com/in/dheeraj-sharma-%F0%9F%87%AE%F0%9F%87%B3-b6bb39249/" target="_blank" className={styles.socialIcons}>
          <FaLinkedin />
        </NavLink>
      </div>

      <p>
        Copyright ©2023 All rights reserved | This is made with <span style={{ color: "var(--github)" }}> ❤ </span>
        by Dheeraj Sharma.
      </p>
    </footer>
  );
};

export default Footer;
